<template>
  <div id="wechat">
    <ul class="wechat-list">
      <msg-item 
        v-for="baseMsgObj in $store.state.msgList.baseMsg"
        :item="baseMsgObj" 
        class="list-row line-bottom"
        :key="baseMsgObj.mid">
      </msg-item>
    </ul>
  </div>
</template>

<script>
import MI from "./mes-item";
export default {
  components: {
    "msg-item": MI,
  }
};
</script>

<style>

</style>



