<template>
    <div class="mes-item">
      <div class="weui-cell">
                <div class="weui-cell__hd" style="position: relative;margin-right: 10px;">
                    <img :src="headerImage" style="width: 50px;display: block">
                    <span class="weui-badge" style="position: absolute;top: -.4em;right: -.4em;">8</span>
                </div>
                <div class="weui-cell__bd">
                    <p>{{name}}</p>
                    <p style="font-size: 13px;color: #888888;">{{mes}}</p>
                </div>
            </div>
    </div>
    
          
</template>

<script>
import headerImage from "images@/headerimages.jpg";
export default {
  // props:["item"],
  data() {
    return {
      // read:this.item.read,
      // deleteMsg:false,
      headerImage: headerImage,
      name:'小名',
      mes:'你好'
    };
  },
  methods: {}
};
</script>
<style lang="scss" scoped>
.mes-item {
  height: 64px;
  // padding: 8px;
  position: relative;
  overflow: hidden;
}
.header-box {
  float: left;
  width: 48px;
  height: 48px;
}
.item-img-size {
  width: 48px;
  height: 48px;
}
</style>




