import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '微信',
      component: resolve => require(['../components/wechat/wechat.vue'], resolve)
    },
    {
      path: '/explore',
      name: '发现',
      component: resolve => require(['../components/explore/index.vue'], resolve)
    },
    {
      path: '/contact',
      name: '通讯录',
      component: resolve => require(['../components/wechat/wechat.vue'], resolve)
    },
    {
      path: '/self',
      name: '我',
      component: resolve => require(['../components/self/self.vue'], resolve)
    },
    {
      path: '/self/settings',
      components: { 'subPage': resolve => require(['../components/self/setting.vue'], resolve) }
    },
    {
      path: '/self/settings/notice',
      components: { "subPage": resolve => require(["../components/self/notice.vue"], resolve) }
    }
  ]
})



